        this.$http({
            url: "group/dianyingshuju/dianyingmingcheng",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].dianyingmingcheng);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].dianyingmingcheng
                    })
                }
                var option = {};
                this.wordclouds(pArray,'dianyingshujuChart1');
				

            }
        });
      })
    },

